<template>
  <v-app style="background-color:#F6F7FF;" >

       <v-navigation-drawer   v-model="drawer"  v-if="logeado==true && sesion==true" :clipped-left="$vuetify.breakpoint.lgAndUp" permanent
    mini-variant
    expand-on-hover
    left app color="opcion1" dark>
        <v-list dense v-if="logeado==true && sesion==true " >
          <template v-for="item in items">
            <v-row  v-if="item.heading" :key="item.heading"  align="center">
              <v-col cols="6">
                <v-subheader v-if="item.heading">
                  {{ item.heading }}
                </v-subheader>
              </v-col>
             <v-col cols="6" class="text-center">
                <a href="#!" class="body-2 black--text">EDIT</a>
              </v-col>
            </v-row>


        

            <!--opcion de unica fila de menu    esta, esta  funcionando-->
            <!-- aqui se van a ver todas las opciones excepto las de administracion -->
            <v-list-item v-if="((item.protegido==false || esAdminstrador==true) && (!item.children))" :key="item.text"  :to="{name:item.to}"  link>
              <v-list-item-action>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>
                  {{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>

                <!-- opcion de menu desplegable--->
            <v-list-group v-else-if="item.children" :key="item.text" v-model="item.model"
              :prepend-icon="item.model ? item.icon : item['icon-alt']" append-icon="">
              <template v-slot:activator>
                <v-list-item-content >
                  <v-list-item-title>
                    {{ item.text }}
                  </v-list-item-title>
                </v-list-item-content>
              </template>
              <v-list-item class="ml-4"  v-for="(child, i) in item.children" :key="i" :to="{name:child.to}" link>
                <v-list-item-action v-if="child.icon">
                  <v-icon small>{{ child.icon }}</v-icon>
                </v-list-item-action>
                <v-list-item-content>
                  <v-list-item-title>
                    {{ child.text }}
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-group>



          </template>
        </v-list>
      </v-navigation-drawer>
  



      <v-app-bar  :clipped-left="$vuetify.breakpoint.lgAndUp" app color="opcion1"  dark>
        <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
        <v-toolbar-title style="width: 300px" class="ml-2 pl-4">
          <v-icon dark>mdi-satellite-variant</v-icon>
          <span class="ml-3">Disatel Tickets</span>
          <v-icon class="ml-5">mdi-ticket-confirmation</v-icon>
        </v-toolbar-title>
        
        <v-spacer></v-spacer>
        <v-btn icon large v-if="logeado!=null" @click="salir()">
         <v-icon>mdi-exit-to-app</v-icon>
        </v-btn>
        <v-btn v-else :to="{name:'login'}" color="success">
          <v-icon>login</v-icon>
        </v-btn>
      </v-app-bar>


      <v-main>

         <v-container class="mx-1">
          
             <router-view />
      


         <v-dialog
                   v-model="loading.estado"
                    hide-overlay
                     persistent
                     width="300">
                        <v-card
                          color="opcion1"
                          dark
                        >
                          <v-card-text>
                          {{loading.titulo}}
                            <v-progress-linear
                              indeterminate
                              color="white"
                              class="mb-0"
                            ></v-progress-linear>
                          </v-card-text>
                        </v-card>
                </v-dialog>


         </v-container>
      </v-main>
      
  </v-app>
</template>

<script>


import { mapState,mapMutations } from "vuex";

export default {
  name: 'App',

  components: {
   
  },

  data: () => ({
    
    dialog: false,
    drawer: null,
    navDrawerPinned:null,
    arrayDrawer:[],
    items: [

       { icon: 'mdi-home', text: 'Inicio', to:'home', protegido:false  },
       { icon: 'mdi-ticket-confirmation-outline', text: 'Ticket abiertos', to:'tickets', protegido:false  },
       { icon: 'mdi-history', text: 'Historial', to:'historial', protegido:false  },
       { icon: 'mdi-account-circle', text: ' Usuarios', to:'usuarios', protegido:true }, //solo admin va verlo

       { icon: 'mdi-cog', text: 'Configuraciones', to:'configuracion', protegido:true }, //solo admin va verlo
     

    ],

     sesion:true

  }),
  computed:{
      ...mapState(['loading']),
      
      logeado(){

        let token_storage=localStorage.getItem('token');

        if(token_storage){
          console.log('se detecto cambio en storage:'+this.$store.state.usuario);
          return true;
        }else{

           console.log('se detecto cambio en storage:'+this.$store.state.usuario+' --->');
          return false;
        }
       
         // return this.$store.state.usuario;
      }, 
      
      esAdminstrador(){
          return this.$store.state.usuario && this.$store.state.usuario.rol=='ADMIN';
      }, 
      
      esUsuarioComun(){
          return this.$store.state.usuario && this.$store.state.usuario.rol=='USER';
      },

   

  },
  created(){
   //this.$store.dispatch("autoLogin");
  
  },


  methods:{
      ...mapMutations(['mostrarLoading','ocultarLoading']),

      salir(){
          this.drawer=false;
          this.$store.dispatch("salir");
          this.sesion=false;
      }

  },
   watch: {

     '$store.state.usuario': function() {
            console.log('watch cambio');
           console.log(this.$store.state.usuario);
              if(this.$store.state.usuario==null){
                this.sesion=false;
                
                this.logeado=false;
              }else{
                this.sesion=true;
              
              }

              
      }

   }
};
</script>


