import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '../store';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'inicio',
    component: () => import('../views/Login.vue'),
    meta:{
      libre:true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue'),
    meta:{
      libre:true
    }
  },

  {
    path: '/home',
    name: 'home',
    component: () => import('../views/Home.vue'),
    meta:{
      administrador:true,
      usuario_comun:true
    }
  },

  {
    path: '/tickets',
    name: 'tickets',
    component: () => import('../views/Tickets.vue'),
    meta:{
      administrador:true,
      usuario_comun:true
    }
  },

  {
    path: '/historial',
    name: 'historial',
    component: () => import('../views/Historial.vue'),
    meta:{
      administrador:true,
      usuario_comun:true
    }
  },

  {
    path: '/usuarios',
    name: 'usuarios',
    component: () => import('../views/Usuarios.vue'),
    meta:{
      administrador:true,
      usuario_comun:false
    }
  },

    {
    path: '/configuracion',
    name: 'configuracion',
    component: () => import('../views/Configuracion.vue'),
    meta:{
      administrador:true,
      usuario_comun:false
    }
  }




]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});





router.beforeEach((to, from, next)=>{



  //si alguien recarga la url debemos tomar ese token e ingresarlo al state

const token_storage=localStorage.getItem('token');



  if(to.matched.some(record=>record.meta.libre)){
    next();


    //validamos rutas solo administrador
  }else if( store.state.usuario && store.state.usuario.rol=='ADMIN'){

    if(to.matched.some(record=>record.meta.administrador)){
      next();
    }

    //ruta para usuarios comunes
  }else if(store.state.usuario && store.state.usuario.rol=='USER'){

    if(to.matched.some(record=>record.meta.usuario_comun)){
          next();
        }

    //si no tiene ningun permiso salir
  }else{
    next({name:'login'});

    
    console.log('entro en ruta libre');

  }

});


export default router
